import {
  Routes,
  Route,
  useNavigationType,
  useLocation,
} from "react-router-dom";
import GhumoreTourismWebsite30May from "./pages/GhumoreTourismWebsite30May";
import GhumoreSearchResults30May from "./pages/GhumoreSearchResults30May";
import GhumoreDetailedProducttour from "./pages/GhumoreDetailedProducttour";
import HomeExperiencesDropdown30M from "./pages/HomeExperiencesDropdown30M";
import HomeDestinationDropdown30M from "./pages/HomeDestinationDropdown30M";
import GhumoreAccountLogin2Jun from "./pages/GhumoreAccountLogin2Jun";
import GhumoreAccountAfterLogin2 from "./pages/GhumoreAccountAfterLogin2";
import HomeContactUs3Jun from "./pages/HomeContactUs3Jun";
import GhumoreDetailedProductPage from "./pages/GhumoreDetailedProductPage";
